import userMixin from '@/mixins/user.js';

export default {
    data() {
        return {
            sidenav: false
        }
    },
    mixins: [userMixin],
    methods: {
        toggleSideNav() {
            this.sidenav ? this.sidenav = false : this.sidenav = true;
        }
    },
    mounted() {
        this.$bus.$on('sidenav', () => {
            this.sidenav = true;
        })
    }     
}
